html {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 30px;
  height: 24px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}
.toggle-button:focus {
  outline: none;
}
.toggle-button__line {
  width: 40px;
  height: 1px;
  background: white;
}
@media (min-width: 700px) {
  .toggle-button__line {
    background: black;
    height: 2.5px;
    border-radius: 25px;
  }
}
.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  padding: 2rem 0;
}
.toolbar__navigation {
  width: 85%;
  margin: auto;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
}
.toolbar__logo {
  width: 50px;
  height: 50px;
}
@media (min-width: 700px) {
  .toolbar {
    padding: 3rem 0;
  }
}
.footer {
  width: 100%;
  background: #004151;
  color: white;
  font-size: 0.8rem;
  text-align: center;
  padding: 3rem 0;
}
.footer-icons {
  width: 55%;
  margin: 0 auto 2rem;
  display: flex;
  justify-content: space-around;
}
.fa:before {
  font-size: 1.7rem;
  cursor: pointer;
  color: white;
}
@media (min-width: 700px) {
  .footer {
    padding: 3em 10%;
    position: absolute;
    bottom: 0;
    left: 0;
    background: none;
    color: grey;
    text-align: left;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
  }
  .footer-icons {
    width: 18%;
    margin: 0;
    justify-content: space-between;
  }
  .fa:before {
    color: grey;
  }
}

.side-drawer {
  height: 100%;
  background: #004151;
  font-size: 2rem;
  font-family: "Poiret One";
  text-transform: uppercase;
  position: fixed;
  right: 0;
  bottom: 0;
  width: 70%;
  max-width: 400px;
  z-index: 2;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  transition: -webkit-transform 0.5s ease-out;
  transition: transform 0.5s ease-out;
  transition: transform 0.5s ease-out, -webkit-transform 0.5s ease-out;
}
.side-drawer.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 20%;
}
.side-drawer li {
  margin: 2rem 0;
}
li {
  margin-bottom: 10px;
}
.menu-link {
  display: inline-block;
  color: #fa923f;
  text-decoration: none;
}
@media (min-width: 700px) {
  .menu-link::after {
    content: "";
    display: block;
    width: 0;
    height: 1px;
    background: #fa923f;
    transition: width 0.3s;
  }
  .menu-link:hover::after {
    width: 100%;
  }
  .side-drawer ul {
    margin-left: 30%;
  }
}

.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0 ,0.3);
  z-index: 1;
}
.wrap-white-line {
  opacity: 0;
  -webkit-animation: fade-in-right ease 1s forwards;
          animation: fade-in-right ease 1s forwards;
}
.wrap-white-line {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}
@-webkit-keyframes fade-in-right {
  from {
    opacity: 0;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
@keyframes fade-in-right {
  from {
    opacity: 0;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}


.button {
  color: white;
  margin-top: 2rem;
  font-family: "Poiret One";
  padding: 1rem 2rem;
  border: .5px solid white;
  background: none;
  border-radius: 5rem;
  font-size: 1rem;
  outline: none;
  cursor: pointer;
  transition: background 1s ease-out;
}
.button:hover {
  background: rgba(35, 224, 225, 0.3);
  border: .5px solid #23e0e1;
}
.picture-section-wrap {
  position: relative;
  height: 700px;
}
.wrap-hello {
  position: absolute;
  color: white;
  left: 0;
  bottom: 0;
  margin: 20% 15%;
}
.hello {
  font-size: 4rem;
}
.hello-subtitle {
  font-size: 1.8rem;
  font-family: "Poiret One";
}
.wrap-img {
  width: 100%;
  height: 100%;
  -webkit-filter: blur(0.5px);
          filter: blur(0.5px);
}
@media (min-width: 700px) {
  .picture-section-wrap {
    width: 65%;
    height: 100vh;
  }
  .wrap-hello {
    margin: 25% 15%;
  }
  .hello {
    font-size: 5rem;
  }
}

.vertical-line {
  display: none;
}
@media(min-width: 700px) {
  .vertical-line {
    display: block;
    width: 1px;
    background: grey;
    height: 100vh;
    margin-left: 4px;
  }
  @-webkit-keyframes grow {
    from {
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
    }
    to {
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
    }
  }
  @keyframes grow {
    from {
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
    }
    to {
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
    }
  }
  .vertical-line {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-animation: grow cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s forwards;
            animation: grow cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s forwards;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
  }
}

.description-section-wrap {
  width: 85%;
  margin: 7rem auto;
}
.description-title {
  font-size: 1.5rem;
  margin-bottom: 2rem;
}
.description-text {
  margin-bottom: 2rem;
}
@media (min-width: 600px) {
  .description-section-wrap {
    flex: 1 1;
    padding: 0 4rem;
  }
  .description-title {
    font-size: 1.2rem;
  }
  .description-text {
    font-size: .8rem;
  }
}
 @media (min-width: 800px) {
  .description-title {
    font-size: 1.6rem;
  }
  .description-text {
    font-size: .9rem;
  }
}

@media (min-width: 700px) {
  .home-wrap {
    display: flex;
    align-items: center;
    background: #f4f4f0;
  }
}



.wrap-project {
  background: black;
  color: white;
}
.wrap-project {
  padding: 8rem 0 2rem;
}
.project-title, .description-of-project {
  width: 80%;
  margin: auto;
  font-size: 2rem;
  margin-bottom: 3rem;
  font-size: 2.5rem;
  font-weight: 400;
  color: #D0D0D0;
}
.description-of-project {
  margin-top: 5rem;
  font-size: 1rem;
  font-weight: normal;
}
@media(min-width: 700px){
  .wrap-project {
    display: flex;
    align-items: center;
    padding: 0;
  }
  .wrap-img-title {
    width: 70%;
    padding: 0 5rem 0 0;
    margin-top: 8rem;
    position: relative;
  }
  .description-of-project {
    height: 100vh;
    flex: 1 1;
    margin: 0;
    background: white;
    color: black;
    display: flex;
    align-items: center;
    padding: 0 5rem;
  }
  .wrap-image-project {
    height: 430px;
  }
  .project-title {
    position: absolute;
    top: -15%;
    left: 0;
    font-size: 3rem;
    color: grey;
  }
}


.first-section-wrap {
  height: 700px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.backgroung-contact {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.wrap-text-button {
  width: 80%;
  margin: auto;
  position: relative;
}
.contact-big-title {
  color: white;
  opacity: 0.1;
  font-size: 4rem;
  font-weight: 900;
  position: absolute;
  top: -40px;
  left: -15px;
}
.contact-title {
  font-size: 3rem;
}
.contact-text {
  margin: 0 0 3rem 0;
}
@media (min-width: 700px) {
  .first-section-wrap {
    height: 100vh;
    width: 70%;
  }
  .wrap-text-button {
    width: 60%;
  }
  .contact-big-title {
    font-size: 6rem;
    top: -60px;
  }
  .wrap-line-contact {
    width: 70%;
  }
}

.second-section-wrap {
  display: flex;
  justify-content: center;
  align-self: center;
  padding: 7rem 0;
}
.london {
  font-size: 1.5rem;
  margin-bottom: 2.5rem;
}
.wrap-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 4rem;
  padding: 2rem;
}
.wrap-icon-text {
  display: flex;
  margin-bottom: 2rem;
}
.txt {
  margin: 0 0 0 1rem;
}
@media (min-width: 700px) {
  .second-section-wrap {
    flex: 1 1;
    height: 100vh;
  }
}

.contact-wrap {
  display: flex;
  flex-direction: column;
}
@media (min-width: 700px) {
  .contact-wrap {
    flex-direction: row;
    align-items: center;
    background: #f4f4f0;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  font-family: Raleway;
}
main {
  flex: 1 1;
}

